import store from '@/store'

export default {
    balanceOf(ownerAddress) {
        return store.state.dtoken.contract.balanceOf(ownerAddress)
    },
    tryBurn(amount) {
        return store.state.dtoken.contract.tryBurn(amount)
    },
    tryMint(fee) {
        return store.state.dtoken.contract.tryMint(fee)
    },
    burn(amount) {
        return store.state.dtoken.contract.connect(store.state.provider.getSigner()).burn(amount)
    }
}
