<template>
  <sec-card style="height: 100%;">
    <div v-if="status === 'init'" class="tx-info-form-panel tx-confirm-panel">
      <div class="tx-confirm-content">
        <slot name="content">

        </slot>
        <div style="width: 100%; margin-top: 20px; text-align: center; display: flex;">
          <el-button v-if="cancelText === ''" class="submit-btn" :size="isMobile ? 'mini' : 'large'" type="primary" round @click="goConfirm">
            {{$t('common.confirm')}}
          </el-button>
          <template v-else>
            <div style="width: 50%;">
              <el-button :size="isMobile ? 'mini' : 'large'" type="primary" round @click="goConfirm">
                {{$t('common.confirm')}}
              </el-button>
            </div>
            <div style="width: 50%; margin-left: 10px;">
              <el-button :size="isMobile ? 'mini' : 'large'" type="info" round @click="goCancel">{{cancelText}}</el-button>
            </div>
          </template>
        </div>
      </div>
    </div>
    <div v-else-if="status === 'pending'" style="display: flex; flex-direction: column; justify-content: center; align-items: center;">
      <img src="../../assets/blue-loader.249140da.svg" alt="loader" class="tx-tip-img loading-img">
      <h2>{{$t('common.waitingTxConfirm')}}</h2>
      <!--        <slot name="info">-->

      <!--        </slot>-->
      <span>{{$t('common.confirmLabel')}}</span>
    </div>
    <div v-else style="display: flex; flex-direction: column; justify-content: center; align-items: center;">
      <img src="../../assets/tx-up.svg" alt="loader" class="tx-tip-img">
      <h2>{{$t('common.txSentLabel')}}</h2>
      <a :href="getExplorerUrl('tx', hash)" target="_blank" class="link-a">{{$t('common.openEtherscan')}}</a>
      <el-button v-if="showAddToken" style="margin-top: 10px;" :size="isMobile ? 'mini' : 'large'" :loading="addTokenLoading" type="primary" @click="goAddToken">
        {{$t('common.add')}} {{addToken.symbol}} {{$t('common.toMetamask')}}
      </el-button>
    </div>
  </sec-card>
</template>
<script>
import SecCard from '../sec_card'
import db from "@/utils/db"
import common from "@/utils/common"
import events from "@/utils/events"
import base from "@/utils/base"
import metamask from "@/utils/metamask"
export default {
  mixins: [base],
  name: 'TxConfirm',
  model: {
    prop: 'hash',
    event: 'change',
  },
  props: {
    txForm: {
      type: Object,
    },
    callFunc: {
      type: Function,
    },
    hash: {
      type: String
    },
    addToken: {
      type: Object
    },
    cancelText: {
      type: String,
      default: ''
    },
    cancelFunc: {
      type: Function,
    }
  },
  components: {
    SecCard
  },
  computed: {
    showAddToken() {
      if (this.addToken === null || this.addToken === undefined) {
        return false
      }
      return true
    }
  },
  data() {
    return {
      status: 'init', // init pending success
      value: '',
      addTokenLoading: false,
    }
  },
  watch: {
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.status = 'init'
      this.value = this.hash
    },
    goConfirm() {
      this.status = 'pending'
      this.callFunc().then(res => {
        this.value = res.hash
        this.$emit('change', this.value)
        let tx = {}
        common.objCopy(this.txForm, tx)
        tx.hash = res.hash
        tx.sender = this.account
        tx.createTime = new Date().getTime()
        db.addTransaction(tx)
        this.status = 'success'
        this.$eventBus.$emit(events.txSent, this.value)
        this.$message.success(this.$t('message.txSendSuccess'))
      }).catch(err => {
        if (err.code === 4001) {
          this.$message.error(this.$t('common.txRejected'))
        } else {
          this.$notify.error({
            title: this.$t('common.sendError'),
            message: err,
            offset: 100,
            duration: 5000,
          })
        }
        this.status = 'init'
      })
    },
    goAddToken() {
      metamask.watchAsset(this.addToken)
      this.addTokenLoading = true
    },
    goCancel() {
      if (this.cancelFunc) {
        this.cancelFunc()
      }
    },
  }
}
</script>
