<template>
  <sec-card :show-background="true" style="max-width: 550px; height: 100%;">
    <div slot="header" class="sec-card-header">
      <div>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-coin"></use>
        </svg>
        <span style="margin-left: 10px;">
          {{$t('token.header.title')}}
        </span>
        <span v-if="!isMobile && dtokenToTokenPriceLabel !== ''" class="tip-span" style="font-size: 15px;" @click="onPriceClick">
          ({{dtokenToTokenPriceLabel}})
        </span>
      </div>
    </div>
    <div>
      <div v-if="isMobile">
        <span class="tip-span" style="font-size: 15px;" @click="onPriceClick">{{dtokenToTokenPriceLabel}}</span>
      </div>
      <div class="sec_form_panel" style="margin-top: 10px;">
        <div class="sec_form_item">
          <div class="sec_form_item_title">
            <span>
              {{$t('token.form.amount')}}:
            </span>
          </div>
          <div class="sec_form_item_content">
            <span class="tip-span">
              {{$t('token.form.balance')}} {{dtokenBalanceInUnit}} {{dtokenSymbol}}
              <a class="link-a" @click="goMax">{{$t('common.max')}}</a>
            </span>
            <amount-input style="min-width: 300px;" v-model="tokenForm.amount" :symbol="dtokenSymbol" :decimals="dtokenDecimals"></amount-input>
            <template v-if="tryBurnAmountInUnit > 0">
              {{$t('token.form.expectedReturn')}} {{tryBurnAmountInUnit}} {{tokenSymbol}}
            </template>
          </div>
        </div>
      </div>
      <div style="width: 100%; margin-top: 20px; text-align: center;">
        <tx-commit-button :tx-loading="txLoading" :tx-form-check="txFormCheck"></tx-commit-button>
      </div>
    </div>
    <el-dialog
        :visible.sync="txConfirmDialogVisible"
        custom-class="wallet-dialog"
        :title="$t('common.txConfirmDialogTitle')"
        width="90%">
      <tx-confirm ref="txConfirm" v-model="hash" :tx-form="txForm" :call-func="txConfirmCall" :cancel-text="$t('token.btn.cancel')" :cancel-func="cancelCommit">
        <div style="width: 100%;" slot="content">
          <div class="sec_form_panel">
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('token.form.amount')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{tokenForm.amount}} {{dtokenSymbol}}
              </div>
            </div>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('token.form.expectedReturn')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{tryBurnAmountInUnit}} {{tokenSymbol}}
              </div>
            </div>
          </div>
          <span style="color: red; line-height: 20px;">{{$t('token.form.tip').replaceAll('%dToken%', ' ' + dtokenSymbol + ' ')}}</span>
        </div>
      </tx-confirm>
    </el-dialog>
  </sec-card>
</template>
<script>
import TxCommitButton from '@/components/common/tx_commit_button'
import AmountInput from '@/components/common/amount_input'
import TxConfirm from '@/components/tx/tx_confirm'
import SecCard from '@/components/sec_card'
import base from "@/utils/base"
import common from "@/utils/common";
import dtoken from "@/utils/dtoken";
import tx_base from "@/utils/tx_base";
import events from "@/utils/events";
export default {
  mixins: [base, tx_base],
  name: 'token',
  props: {
  },
  components: {
    SecCard,
    TxConfirm,
    AmountInput,
    TxCommitButton
  },
  computed: {
    amountInWei() {
      const amountInWei = common.unitToWeiWithDecimals(this.tokenForm.amount, this.dtokenDecimals)
      return amountInWei
    },
    dtokenBalanceInUnit() {
      return common.weiToUnitWithDecimals(this.dtokenBalance, this.dtokenDecimals)
    },
    tryBurnAmountInUnit() {
      if (this.amountInWei <= 0) {
        return 0
      }

      return (this.dtokenToTokenPrice * this.tokenForm.amount).toFixed(this.tokenDecimals)
    },
    dtokenToTokenPriceLabel() {
      if (this.dtokenToTokenPrice === 0) {
        return ''
      }

      if (this.priceType === 'toToken') {
        return `1${this.dtokenSymbol}=${this.dtokenToTokenPrice}${this.tokenSymbol}`
      } else {
        return `1${this.tokenSymbol}=${this.tokenToDtokenPrice.toFixed(this.dtokenDecimals)}${this.dtokenSymbol}`
      }
    }
  },
  data() {
    return {
      dtokenBalance: 0,
      priceType: 'toToken',
      dtokenToTokenPrice: 0,
      tokenToDtokenPrice: 0,
      dtokenToTokenPriceInWei: 0,
      tokenForm: {
        amount: '',
      },
      txConfirmDialogVisible: false,
    }
  },
  created() {
    this.eventHandlers[events.txConfirmed] = async param => {
      if (param.type === common.getTxTypes().burnDtoken) {
        await common.syncAccountTokenInfo()
        await this.init()
      }
      if (this.hash === param.hash) {
        this.txPending = false
      }
    }
    this.eventHandlers[events.txSent] = async param => {
      if (this.hash === param) {
        this.tokenForm.amount = ''
      }
    }
    this.eventHandlers[events.dTokenInited] = () => {
      this.baseInit()
    }
  },
  methods: {
    baseInit() {
      this.init()
    },
    async init() {

      if (null !== this.poolItem) {
        this.dtokenBalance = await dtoken.balanceOf(this.account)
        // this.tokenForm.amount = this.dtokenBalanceInUnit
        await this.getPrice()
      }

    },
    goMax() {
      this.tokenForm.amount = this.dtokenBalanceInUnit
    },
    txFormCheck() {
      let r = {
        success: false
      }
      if (this.amountInWei <= 0) {
        r.msg = this.$t('token.btn.inputAmount')
        return r
      }
      if (this.amountInWei.gt(this.dtokenBalance)) {
        r.msg = this.$t('common.balanceNotEnough')
        return r
      }

      r.success = true
      r.msg = this.$t('token.btn.burn')
      r.func = this.onCommit
      return r
    },
    async getPrice() {
      const price = await dtoken.tryBurn(common.unitToWeiWithDecimals(1, this.dtokenDecimals))
      this.dtokenToTokenPriceInWei = price
      this.dtokenToTokenPrice = common.weiToUnit(price)
      if (price.lte(common.toBigNumber(0))) {
        this.tokenToDtokenPrice = 0
      } else {
        const p = common.unitToWeiWithDecimals(1, this.dtokenDecimals).div(price).toNumber()
        this.tokenToDtokenPrice = p
      }
    },
    onPriceClick() {
      if (this.priceType === 'toToken') {
        this.priceType = 'toDtoken'
      } else {
        this.priceType = 'toToken'
      }
    },
    cancelCommit() {
      this.txConfirmDialogVisible = false
    },
    onCommit() {

      if (this.tokenForm.amount > this.dtokenBalanceInUnit) {
        this.$message.warning(this.$t('common.balanceNotEnough'))
        return
      }

      if (this.$refs['txConfirm']) {
        this.$refs['txConfirm'].init()
      }

      this.txForm = {
        type: common.getTxTypes().burnDtoken,
        to: this.dtokenAddress,
        amount: common.unitToWeiWithDecimals(this.tokenForm.amount, this.dtokenDecimals),
      }
      this.txConfirmCall = () => {
        return dtoken.burn(this.txForm.amount)
      }
      this.txConfirmDialogVisible = true
    }
  }
}
</script>
