<template>
  <div style="display: flex; flex-direction: row; align-items: center;">
    <input type="text" class="sec_amount_input" @input="onInput" @focus="onFocus"
           :style="style"
           :placeholder="decimals === 0 ? '0' : '0.0'" v-model="amount" controls="false">
    <span v-if="symbol" style="margin-left: 5px;">{{symbol}}</span>
  </div>
</template>
<script>
import base from "@/utils/base"
import common from "@/utils/common"

export default {
  mixins: [base],
  name: 'AmountInput',
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    value: {
      type: [Number, String]
    },
    symbol: String,
    decimals: {
      type: Number,
      default: 79,
    },
    lineHeight: {
      type: Number,
      default: 30,
    }
  },
  data() {
    return {
      amount: '',
      lastAmount: '',
    }
  },
  computed: {
    style() {
      return {
        'line-height': this.lineHeight + 'px',
        'min-width': this.symbol === '%' ? '0' : '220px',
        'text-align': this.symbol === '%' ? 'center' : 'left',
      }
    }
  },
  created() {
    this.amount = this.value
    this.lastAmount = this.value
  },
  watch: {
    // amount(val) {
    //   this.$emit('change', val)
    // },
    value(val) {
      this.amount = val
      this.lastAmount = val
    }
  },
  methods: {
    onFocus() {
    },
    onInput(event) {
      const val = event.target.value
      if (val === '') {
        this.lastAmount = val
      } else if (this.checkValue(val)) {
        this.lastAmount = val
      } else {
        this.amount = this.lastAmount
      }
      this.$emit('change', this.amount)
    },
    checkValue(val) {
      if (this.decimals === 0) {
        if (typeof val === 'string') {
          return parseInt(val, 10).toString() === val
        } else {
          return parseInt(val, 10) === val
        }
      } else {
        try {
          common.unitToWeiWithDecimals(val, this.decimals)
          return true
        } catch (e) {
          return false
        }
      }
    }
  }
}
</script>
