<template>
  <div>
    <el-button class="submit-btn" :loading="txLoading" :type="sellBtnType" :size="isMobile ? 'mini' : 'large'"
               :disabled="sellBtnType !== 'primary'" round @click="onCommit">{{sellBtnTitle}}</el-button>

    <el-dialog
        :visible.sync="installWalletDialogVisible"
        custom-class="wallet-dialog"
        append-to-body
        width="90%">
      <div style="display: flex; align-items: center; justify-content: center; flex-direction: column;">
        <div class="wallet-tip" v-html="$t('common.installWalletTip')">
        </div>
        <el-button :size="isMobile ? 'mini' : 'large'" type="primary" @click="goInstallWallet">{{$t('common.installWallet')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import base from "@/utils/base"
import common from "@/utils/common";

export default {
  mixins: [base],
  name: 'TxCommitButton',
  props: {
    isAccount: {
      type: Boolean,
      default: false
    },
    txLoading: Boolean,
    txFormCheck: Function,
    size: {
      type: String,
      default: 'small'
    }
  },
  data() {
    return {
      installWalletDialogVisible: false,
    }
  },
  computed: {
    sellBtnType() {
      if (this.account === '') {
        return 'primary'
      }
      if (!this.isAccount && this.poolItem === null) {
        return 'info'
      }
      if (this.txFormCheck) {
        if (!this.txFormCheck().success) {
          return 'info'
        }
      }
      return 'primary'
    },
    sellBtnTitle() {
      if (this.account === '') {
        return this.$t('header.connectWallet')
      }
      if (this.txLoading) {
        return this.$t('common.loading')
      }
      if (!this.isAccount) {
        if (this.poolItem === null) {
          return this.$t('common.noPool')
        }
      }
      if (this.txFormCheck) {
        const checkResult = this.txFormCheck()
        if (checkResult.msg === undefined || checkResult.msg === null || checkResult.msg === '') {
          return this.$t('common.submit')
        }
        return checkResult.msg
      }

      return this.$t('common.submit')
    },
  },
  watch: {
  },
  methods: {
    onCommit() {
      if (this.account === '') {
        return this.onConnectWallet()
      }

      if (!this.isAccount && this.poolItem === null) {
        return
      }

      if (this.txFormCheck) {
        const checkResult = this.txFormCheck()
        if (checkResult.success && checkResult.func) {
          checkResult.func()
        }
      }
    },
    async onConnectWallet() {

      await common.getAccount(this.$message, () => {
        this.installWalletDialogVisible = true
      })
    },
  }
}
</script>
<style>
.wallet-tip {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  color: #fff;
  max-height: 60vh;
  padding: 26px 24px;
  overflow-y: auto;
  font-size: 15px;
  line-height: 20px;
  white-space: pre-wrap;
  text-align: center;
  word-break: break-word;
  -webkit-overflow-scrolling: touch;
}
</style>
