import events from "@/utils/events"

export default {
    created() {
        this.eventHandlers[events.txConfirmed] = param => {
            if (this.hash === param.hash) {
                this.txPending = false
            }
        }
        this.eventHandlers[events.txSent] = sentHash => {
            if (this.hash === sentHash) {
                this.txPending = true
            }
        }
    },
    beforeDestroy() {
    },
    data() {
        return {
            eventHandlers: {},
            hash: '',
            txType: '',
            txPending: false,
            txForm: {},
            txConfirmCall: null,
            addToken: null,
        }
    },
    computed: {
        txLoading() {
            return this.$store.state.loading || this.txPending
        },
        initLoading() {
            return this.$store.state.loading
        }
    },
    methods: {
    }
}
